/* ----- Set the base font size on the HTML element for easier use of rem, em, and px. ----- */
html {
  font-size: 62.5%; /* 1rem = 10px (so, 1.6rem = 16px) */
  box-sizing: border-box;
  width: 100%;
  scroll-behavior: smooth;
  scroll-padding-top: 0rem;
}

/* ----- Inherit box-sizing in all elements and pseudo-elements ----- */
*,
*::before,
*::after {
  box-sizing: inherit;
}

/* ----- Set margins and padding in the body to avoid unwanted spaces ----- */
body {
  font-optical-sizing: auto;
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-size: 1.8rem;
  color: #000000;
  margin: 0;
  padding: 0;
  height: 100%;
  line-height: 1.8;
  background-color: #0c0c0d;
}

/** ----- Global styles ----- **/
p {
  font-size: 2rem;
  color: #000000;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  margin: 5rem;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

img {
  height: auto;
  display: block;
}

/** Media Queries **/
.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  padding: 5rem;
}

.reset-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.none-display {
  display: none;
}

.button {
  background: none;
  border: none;
}

.nav {
  position: absolute;
  height: 50%;
  width: 100%;
  background-color: #131313;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateY(-100%);
  transition: 0.5s;
  z-index: 1;
}
@media (min-width: 768px) {
  .nav {
    position: fixed;
    top: 0%;
    left: 50%;
    transform: translateX(-50%);
    height: 5rem;
    width: 60%;
    margin: 2.5rem auto;
    padding: 1rem 5rem;
    border-radius: 5rem;
  }
}
@media (min-width: 1200px) {
  .nav {
    width: 40%;
  }
}
.nav ul {
  display: inherit;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nav ul {
    flex-direction: row;
    gap: 4rem;
  }
}
.nav ul li {
  display: inherit;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
.nav ul li:last-child {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .nav ul li {
    margin-top: 0rem;
    gap: 1rem;
  }
  .nav ul li:last-child {
    margin-top: 0rem;
  }
}
.nav ul li a {
  color: #b292ff;
  font-size: 2.5rem;
  transition: 0.2s;
}
.nav ul li a:hover {
  color: #ffffff;
  transform: scale(1.2);
}
.nav ul li .nav__img__link img {
  width: 5rem;
  height: 5rem;
}
@media (min-width: 1200px) {
  .nav ul li .nav__img__link img {
    width: 3rem;
    height: 3rem;
  }
}
.nav ul li .nav__img__link:hover {
  transform: translateY(-0.5rem);
  transform: scale(1.2);
  filter: brightness(0) invert(1);
}

.nav__button {
  position: absolute;
  top: 5rem;
  right: 5rem;
  width: 5rem;
  filter: brightness(100) invert(0);
  transition: 0.2s;
  transform: scale(1.3);
  z-index: 1;
}
.nav__button:hover, .nav__button:focus {
  cursor: pointer;
  filter: none;
}
.nav__button img {
  width: 3.5rem;
  height: 3.5rem;
}
@media (min-width: 768px) {
  .nav__button {
    display: none;
  }
}

.nav.active {
  transform: translateY(0);
}

@media (min-width: 768px) {
  .nav.shrunk {
    transform: translateX(-50%) scale(0.8);
    opacity: 0.8;
    transition: transform 0.3s ease, opacity 0.3s ease;
  }
}

.hero {
  display: flex;
  flex-direction: column-reverse;
  color: #ffffff;
}
@media (min-width: 1200px) {
  .hero {
    flex-direction: row;
    margin-top: 15rem;
  }
}
@media (min-width: 1400px) {
  .hero {
    flex-direction: row;
  }
}
.hero .hero__info {
  min-height: 30rem;
}
.hero .hero__info h1 {
  margin: 0;
  font-weight: 400;
  font-size: 5rem;
  text-align: center;
}
@media (min-width: 1200px) {
  .hero .hero__info h1 {
    text-align: left;
  }
}
.hero .hero__info p {
  color: #6f6f6f;
  font-weight: 300;
  text-align: center;
}
@media (min-width: 1200px) {
  .hero .hero__info p {
    text-align: left;
    font-size: 2.5rem;
  }
}
.hero .hero__info p span {
  font-weight: 400;
  color: #ffffff;
}
.hero .hero__buttons__container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1200px) {
  .hero .hero__buttons__container {
    flex-direction: row;
    gap: 5rem;
    margin-top: 5rem;
  }
}
.hero .hero__buttons__container .hero__button {
  display: inherit;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  background-color: #ffffff;
  height: 5rem;
  padding: 1rem 4rem;
  border-radius: 5rem;
  transition: transform 0.2s ease-in-out;
}
@media (min-width: 768px) {
  .hero .hero__buttons__container .hero__button {
    width: 35rem;
    margin: auto;
  }
}
@media (min-width: 1200px) {
  .hero .hero__buttons__container .hero__button {
    padding: 0.5rem 4rem;
  }
}
.hero .hero__buttons__container .hero__button--cv {
  color: #000000;
}
.hero .hero__buttons__container .hero__button--linkedin {
  display: inherit;
  gap: 1rem;
  color: #b292ff;
  background: linear-gradient(to bottom, #232323, #434343, #232323);
}
.hero .hero__buttons__container .hero__button--linkedin img {
  width: 3.5rem;
  height: 4rem;
}
.hero .hero__buttons__container .hero__button:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.hero picture {
  display: flex;
  justify-content: end;
}
@media (min-width: 768px) {
  .hero picture {
    width: 50rem;
    margin: auto;
  }
}
.hero picture img {
  width: 100%;
  height: auto;
  aspect-ratio: 1/1;
  filter: drop-shadow(0px 0px 100px rgba(255, 255, 255, 0.5));
}
@media (min-width: 1200px) {
  .hero picture img {
    width: 35rem;
  }
}
@media (min-width: 1200px) {
  .hero section,
  .hero picture {
    width: 50%;
  }
}

.projects {
  color: #ffffff;
  position: relative;
  height: 85rem;
  transition: 0.2s;
  overflow: hidden;
}
@media (min-width: 1200px) {
  .projects {
    height: 70rem;
  }
}
.projects__tittle {
  margin: 0;
  font-size: 3rem;
}
@media (min-width: 1200px) {
  .projects__tittle {
    margin-bottom: 0;
    font-size: 3.4rem;
  }
}
@media (min-width: 1200px) {
  .projects picture {
    margin-top: 2.5rem;
  }
}
.projects picture img {
  width: 100%;
  height: 25rem;
  border-radius: 5rem;
}
@media (min-width: 1200px) {
  .projects picture img {
    width: 40rem;
  }
}
.projects__container {
  transition: 0.5s;
  position: relative;
}
.projects__content {
  position: absolute;
  top: 100%;
  width: 100%;
  height: 50rem;
}
.projects__content--1 {
  transform: translateX(-110%);
}
.projects__content--2 {
  transform: translateX(110%);
}
.projects__item {
  background-color: #111111;
  padding: 1rem;
  border-radius: 5rem;
  position: absolute;
  width: 90%;
  left: 50%;
  transform: translateX(-50%);
}
.projects__item:nth-child(1) {
  top: 10%;
}
.projects__item:nth-child(2) {
  top: 70%;
}
@media (min-width: 1200px) {
  .projects__item {
    padding: 5rem;
    width: 50rem;
    top: 50%;
    left: auto;
    transform: translateY(-50%);
  }
  .projects__item:nth-child(1) {
    left: 3.5rem;
    top: 60%;
    transform: translateY(-50%);
  }
  .projects__item:nth-child(2) {
    right: 2.5rem;
    top: 60%;
    transform: translateY(-50%);
  }
}
.projects__name {
  margin: 0;
  display: none;
}
@media (min-width: 1200px) {
  .projects__name {
    display: block;
  }
}
.projects__date {
  color: #6f6f6f;
  margin: 0;
  line-height: 0;
  font-size: 1.5rem;
  display: none;
}
@media (min-width: 1200px) {
  .projects__date {
    display: block;
  }
}
.projects__move_button {
  position: absolute;
  height: 50rem;
  top: 30%;
  width: 5rem;
  opacity: 0.2;
  transition: 0.2s;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0.1019607843);
  border-radius: 5rem;
}
@media (min-width: 768px) {
  .projects__move_button {
    top: 20%;
  }
}
@media (min-width: 1200px) {
  .projects__move_button {
    width: 3.5rem;
    background: none;
  }
}
.projects__move_button:hover, .projects__move_button:focus {
  cursor: pointer;
  opacity: 1;
}
.projects__move_button:hover img, .projects__move_button:focus img {
  transform: scale(3.5);
}
.projects__move_button--1 {
  left: 1rem;
  transform: rotate(180deg);
}
.projects__move_button--2 {
  right: 1rem;
}
.projects__move_button img {
  transition: 0.2s;
  transform: scale(3);
  width: 3rem;
  height: 3rem;
}

.skills {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  align-items: center;
  color: #ffffff;
}
.skills__container {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 768px) {
  .skills__container {
    flex-direction: row;
    justify-content: center;
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .skills__container {
    gap: 5rem;
  }
}
.skills__title {
  margin: 0;
  text-align: center;
}
@media (min-width: 1200px) {
  .skills__title {
    text-align: left;
  }
}
.skills__sub__title {
  font-size: 2rem;
  margin: 0;
}
@media (min-width: 1200px) {
  .skills__sub__title {
    font-size: 2.5rem;
  }
}
.skills__content {
  border: solid #ffffff 0.1rem;
  border-radius: 5rem;
  padding: 2rem 3rem;
  transition: 0.2s;
}
.skills__content:hover {
  transform: scale(1.1);
}
@media (min-width: 1200px) {
  .skills__content {
    padding: 2.5rem 5rem;
  }
}
.skills__list li {
  display: flex;
  align-items: center;
  justify-content: left;
  gap: 2rem;
  transition: 0.2s;
}
.skills__list li:hover {
  transform: scale(1.01);
}
@media (min-width: 1200px) {
  .skills__list li:hover {
    transform: scale(1.1);
  }
}
.skills__list li p {
  color: #ffffff;
}
.skills__list li img {
  width: 3rem;
  height: 3rem;
}
.skills__list li div {
  border-radius: 5rem;
  background-color: #272727;
  padding: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer .footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  border-radius: 5rem;
  padding: 0.5rem 5rem;
  font-family: "Montserrat", sans-serif;
  background-color: #111111;
}
@media (min-width: 768px) {
  footer .footer__container {
    flex-direction: row;
  }
}
footer .footer__container ul {
  display: inherit;
  gap: 2rem;
  list-style: none;
}
footer .footer__container ul li {
  transition: 0.2s;
}
footer .footer__container ul li:hover {
  transform: scale(1.1);
}
footer div {
  display: flex;
  flex-direction: row;
}
footer p {
  color: #ffffff;
  text-align: center;
  margin: 0.5rem 0;
}

.footer__icons {
  width: 5rem;
  height: 5rem;
}
@media (min-width: 768px) {
  .footer__icons {
    width: 3rem;
    height: 3rem;
  }
}